import React from "react";

const Header = ({ title, showWelcome }) => {
  const employeeName = "John Doe";

  return (
    <div className="p-4 border-b border-gray-200">
      <div className="flex items-center justify-between">
        <div className="space-y-1">
          <h1 className="text-2xl font-bold">{title}</h1>
          {showWelcome && (
            <p className="text-gray-600">Welcome back, {employeeName}</p>
          )}
        </div>

        <div className="flex items-center gap-4">
          {/* <div className="relative">
            <input
              type="text"
              placeholder="Search Name or Description"
              className="pl-10 pr-4 py-2 border rounded-lg w-[400px] focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5 h-5" />
          </div> */}

          <div className="flex items-center gap-2">
            <div className="w-8 h-8 bg-gray-200 rounded-full p-1">👤</div>
            <span className="text-gray-600">{employeeName}</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
