import { useState } from "react";
import "./App.css";
import ResidentPortal from "./Resident/ResidentPortal";
import MarketingPortal from "./Marketing/MarketingPortal";

function App() {
  const [portal, setPortal] = useState();
  const onLogOut = () => {
    setPortal("");
  };
  if (portal === "Marketing") {
    return <MarketingPortal onLogOut={onLogOut}></MarketingPortal>;
  }
  if (portal === "Resident") {
    return <ResidentPortal onLogOut={onLogOut}></ResidentPortal>;
  }
  return (
    <>
      <h1>Select A Portal</h1>
      <button
        onClick={() => setPortal("Marketing")}
        className="w-1/4 py-4 m-8 text-center bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
      >
        Marketing Portal
      </button>
      <br></br>
      <button
        onClick={() => setPortal("Resident")}
        className="w-1/4 py-4 m-8 text-center bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors"
      >
        Resident Portal
      </button>
    </>
  );
}

export default App;
