import React, { useState } from "react";
import { Plus } from "lucide-react";
import CalendarHeader from "./Calendar Header";
import WeekView from "./Week View";
import EventsList from "./Events List";
import DeleteConfirmModal from "./Delete Confirm Modal";
import { INITIAL_EVENTS } from "./data";
import EventForm from "./Event Form";

const Calendar = () => {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [showMonthPicker, setShowMonthPicker] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(null);
  const [events, setEvents] = useState(INITIAL_EVENTS);
  const [showAddEvent, setShowAddEvent] = useState(false);

  const handleDeleteEvent = (eventId) => {
    setEvents(events.filter((event) => event.id !== eventId));
    setShowDeleteConfirm(null);
  };

  const selectedDateEvents = events.filter(
    (event) => event.date.toDateString() === selectedDate.toDateString()
  );

  const handleUpdateEvent = (updatedEvent) => {
    const newEvents = events.map((event) =>
      event.id === updatedEvent.id ? updatedEvent : event
    );
    // Sort events by time before updating state
    setEvents(
      newEvents.sort((a, b) => {
        const aStartTime = parseTime(a.startTime);
        const bStartTime = parseTime(b.startTime);

        if (aStartTime === bStartTime) {
          const aEndTime = parseTime(a.endTime);
          const bEndTime = parseTime(b.endTime);
          return aEndTime - bEndTime;
        }

        return aStartTime - bStartTime;
      })
    );
  };

  const handleAddEvent = (newEvent) => {
    const latestId = Math.max(...events.map((e) => e.id), 0);
    const eventToAdd = {
      ...newEvent,
      id: latestId + 1,
    };

    setEvents(
      [...events, eventToAdd].sort((a, b) => {
        const aStartTime = parseTime(a.startTime);
        const bStartTime = parseTime(b.startTime);

        if (aStartTime === bStartTime) {
          const aEndTime = parseTime(a.endTime);
          const bEndTime = parseTime(b.endTime);
          return aEndTime - bEndTime;
        }

        return aStartTime - bStartTime;
      })
    );
  };
  const parseTime = (timeStr) => {
    const [time, period] = timeStr.split(" ");
    let [hours, minutes] = time.split(":");
    hours = parseInt(hours);
    if (period === "PM" && hours !== 12) hours += 12;
    if (period === "AM" && hours === 12) hours = 0;
    return hours * 60 + parseInt(minutes);
  };

  return (
    <div className=" w-1/3 2xl:w-1/3 bg-white rounded-3xl p-6 shadow-lg mt-4">
      <CalendarHeader
        currentDate={currentDate}
        setCurrentDate={setCurrentDate}
        selectedDate={selectedDate}
        setSelectedDate={setSelectedDate}
        showMonthPicker={showMonthPicker}
        setShowMonthPicker={setShowMonthPicker}
        events={events}
      />

      <WeekView
        currentDate={currentDate}
        selectedDate={selectedDate}
        setSelectedDate={setSelectedDate}
        events={events}
      />

      <div className="mt-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-gray-600">
            Activities on{" "}
            {selectedDate.toLocaleDateString("en-US", {
              weekday: "long",
              month: "long",
              day: "numeric",
              year: "numeric",
            })}
          </h3>
          <button className="text-orange-500">
            <svg className="w-6 h-6" viewBox="0 0 24 24">
              <path
                fill="currentColor"
                d="M13 9h-2V7h2m0 10h-2v-6h2m-1-9A10 10 0 0 0 2 12a10 10 0 0 0 10 10 10 10 0 0 0 10-10A10 10 0 0 0 12 2"
              />
            </svg>
          </button>
        </div>

        <EventsList
          events={selectedDateEvents}
          onDeleteClick={setShowDeleteConfirm}
          onUpdateEvent={handleUpdateEvent}
        />

        <DeleteConfirmModal
          showDeleteConfirm={showDeleteConfirm}
          onCancel={() => setShowDeleteConfirm(null)}
          onConfirm={handleDeleteEvent}
        />

        <div className="mt-6 space-y-2">
          {/* <button className="w-full py-3 bg-orange-500 text-white rounded-lg flex items-center justify-center gap-2">
            <Eye size={20} />
            See More
          </button> */}
          <button
            onClick={() => setShowAddEvent(true)}
            className="w-full py-3 bg-orange-500 text-white rounded-lg flex items-center justify-center gap-2"
          >
            <Plus size={20} />
            Add Event
          </button>
          {showAddEvent && (
            <EventForm
              event={{
                title: "",
                type: "tour",
                startTime: "9:00 AM",
                endTime: "10:00 AM",
                location: "",
                date: new Date(selectedDate.getTime()),
              }}
              onSubmit={handleAddEvent}
              onClose={() => setShowAddEvent(false)}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default Calendar;
