const parseTime = (timeStr) => {
  const [time, modifier] = timeStr.split(" ");
  let [hours, minutes] = time.split(":");
  hours = parseInt(hours);

  if (modifier === "PM" && hours < 12) hours += 12;
  if (modifier === "AM" && hours === 12) hours = 0;

  return hours * 60 + parseInt(minutes || 0);
};

const compareEvents = (a, b) => {
  if (a.startTime !== b.startTime) {
    return parseTime(a.startTime) - parseTime(b.startTime);
  }
  return parseTime(a.endTime) - parseTime(b.endTime);
};

export const convertTo24Hour = (time12h) => {
  const [rawTime, period] = time12h.split(" ");
  let [hours, minutes] = rawTime.split(":");
  hours = parseInt(hours);

  if (period === "PM" && hours !== 12) hours += 12;
  if (period === "AM" && hours === 12) hours = 0;

  return `${String(hours).padStart(2, "0")}:${minutes}`;
};

export const convertTo12Hour = (time24h) => {
  const [hours, minutes] = time24h.split(":");
  const hour12 = parseInt(hours) % 12 || 12;
  const period = parseInt(hours) < 12 ? "AM" : "PM";
  return `${hour12}:${minutes} ${period}`;
};

