function ResidentPortal() {
  const [eventDetail, setEventDetail] = useState();
  const [showSearch, setShowSearch] = useState(false);
  const [showAccount, setShowAccount] = useState(false);
  const [showActivities, setShowActivities] = useState(false);
  const [showRequest, setShowRequest] = useState(false);
  const [showFinance, setShowFinance] = useState(false);
  const [resident, setResident] = useState({
    preferredName: "John Doe",
    legalName: "Jonathan Doe Sr",
    primaryPhone: "(678) 555-5555",
    building: "A - Villas",
    dateOfBirth: "12/25/1950",
    room: "323",
    emergencyContact: "Jane Doe",
    relation: "Daughter",
    emergencyPhone: "(678) 777-7777",
    picture: "profile-image.png",
  });
  function onSetEventDetail(event) {
    if (eventDetail) {
      setEventDetail(null);
    } else {
      setEventDetail(event);
    }
  }
  function onToggleSearch() {
    setEventDetail(null);
    setShowAccount(false);
    setShowActivities(false);
    setShowRequest(false);
    setShowSearch(!showSearch);
    setShowFinance(false);
  }
  function onToggleAccount() {
    setEventDetail(null);
    setShowAccount(!showAccount);
    setShowActivities(false);
    setShowRequest(false);
    setShowSearch(false);
    setShowFinance(false);
  }
  function onToggleActivities() {
    setEventDetail(null);
    setShowAccount(false);
    setShowActivities(!showActivities);
    setShowSearch(false);
    setShowFinance(false);
    setShowRequest(false);
  }
  function onToggleRequest() {
    setEventDetail(null);
    setShowAccount(false);
    setShowActivities(false);
    setShowSearch(false);
    setShowFinance(false);
    setShowRequest(!showRequest);
  }
  function onToggleFinance() {
    setEventDetail(null);
    setShowAccount(false);
    setShowActivities(false);
    setShowSearch(false);
    setShowFinance(!showFinance);
    setShowRequest(false);
  }
  return (
    <>
      <BannerImage img="Resident-Portal-Background.png" />
      <div className="bg-slate-100 pb-[100px] lg:px-[100px] ">
        <div className="flex flex-row flex-wrap justify-center items-stretch gap-24 lg:-mt-60 ">
          <ResidentCard
            resident={resident}
            onToggleAccount={onToggleAccount}
          ></ResidentCard>

          <PaymentCard />
        </div>

        <div className="p-4 bg-gray-100 min-h-screen mt-16">
          <h1 className="text-xl font-bold mb-8">Community Details</h1>
          <div className="columns-2 lg:columns-4 xl:columns-4 2xl:columns-4 gap-4 mx-auto max-w-7xl">
            {events.map((events, index) => (
              <div key={index} className="mb-4 break-inside-avoid">
                <EventCard event={events} onSetEventDetail={onSetEventDetail} />
              </div>
            ))}
          </div>
        </div>
        {eventDetail && (
          <EventDetailCard
            event={eventDetail}
            onSetEventDetail={onSetEventDetail}
          ></EventDetailCard>
        )}

        {showSearch && (
          <HelpCenter onToggleSearch={onToggleSearch}></HelpCenter>
        )}
        {showAccount && (
          <AccountCenter
            onToggleAccount={onToggleAccount}
            resident={resident}
            onSetResident={setResident}
          ></AccountCenter>
        )}
        {showActivities && (
          <CommunityCenter
            onToggleActivities={onToggleActivities}
            events={events}
            onSetEventDetail={onSetEventDetail}
          ></CommunityCenter>
        )}
        {showRequest && (
          <RequestCenter onToggleRequest={onToggleRequest}></RequestCenter>
        )}
        {showFinance && (
          <FinanceCenter onToggleFinance={onToggleFinance}></FinanceCenter>
        )}
        <BottomNavbar
          onToggleSearch={onToggleSearch}
          onToggleAccount={onToggleAccount}
          onToggleActivities={onToggleActivities}
          onToggleRequest={onToggleRequest}
          onToggleFinance={onToggleFinance}
        />
      </div>
    </>
  );
}

export default ResidentPortal;
