const Layout = ({ children, activeTab, setActiveTab, onLogOut }) => {
  const topMenuItems = [
    { id: "overview", icon: "metrics.png", label: "Overview" },
    { id: "leads", icon: "bars.png", label: "Leads & Tours" },
    { id: "calendar", icon: "calendar.png", label: "Calendar" },
    { id: "analytics", icon: "line-chart.png", label: "Analytics" },
  ];

  const MenuItem = ({ item }) => {
    const isActive = activeTab === item.id;

    return (
      <button
        className={`w-full h-24 flex flex-col items-center justify-center cursor-pointer relative
          ${isActive ? "z-10" : ""}
        `}
        onClick={() => setActiveTab(item.id)}
      >
        <div
          className={`flex flex-col items-center justify-center w-full h-20 rounded-lg
            ${isActive ? "bg-white shadow-lg transform translate-x-2" : ""}
            ${isActive ? "text-orange-500" : "text-gray-400"}
          `}
        >
          <img
            src={
              isActive
                ? "icons/active/" + item.icon
                : "icons/inactive/" + item.icon
            }
            alt="icon"
            className="max-h-1/2 mb-2"
          ></img>

          <span className="text-sm w-full text-center">{item.label}</span>
        </div>
      </button>
    );
  };

  return (
    <div className="flex h-svh bg-gray-50 overflow-auto">
      {/* Sidebar */}
      <nav className="fixed left-0 top-0 h-full w-32  2xl:w-48 bg-white shadow-lg flex flex-col z-50">
        <div className="flex-1 flex flex-col mt-4">
          {topMenuItems.map((item) => (
            <MenuItem key={item.id} item={item} />
          ))}
        </div>

        <div className="mb-4" onClick={() => onLogOut()}>
          <MenuItem
            item={{ id: "logout", icon: "logout.png", label: "Logout" }}
          />
        </div>
      </nav>

      {/* Main content area */}
      <main className="flex-1 ml-32 2xl:ml-48 p-8 ">{children}</main>
    </div>
  );
};

export default Layout;
