export const getWeekDates = (date) => {
  const dates = [];
  const weekStart = new Date(date);
  weekStart.setDate(date.getDate() - date.getDay());

  for (let i = 0; i < 7; i++) {
    const date = new Date(weekStart);
    date.setDate(weekStart.getDate() + i);
    dates.push(date);
  }

  return dates;
};

export const generateMonthDates = (year, month) => {
  const firstDay = new Date(year, month, 1);
  const lastDay = new Date(year, month + 1, 0);
  const days = [];

  for (let i = 0; i < firstDay.getDay(); i++) {
    days.push(null);
  }

  for (let i = 1; i <= lastDay.getDate(); i++) {
    days.push(new Date(year, month, i));
  }

  return days;
};

export const isSelected = (date, selectedDate) => {
  return date?.toDateString() === selectedDate?.toDateString();
};

export const isToday = (date) => {
  const today = new Date();
  return date?.toDateString() === today.toDateString();
};

export const hasEventsOnDate = (date, events) => {
  return (
    date &&
    events.some((event) => event.date.toDateString() === date.toDateString())
  );
};

export const isInCurrentWeek = (date, currentDate) => {
  if (!date) return false;
  const weekDates = getWeekDates(currentDate);
  return weekDates.some(
    (weekDate) => weekDate.toDateString() === date.toDateString()
  );
};
