import React from "react";
import { Search, Bell, Plus, DollarSign, User } from "lucide-react";

function BottomNavbar({
  onToggleSearch,
  onToggleAccount,
  onToggleActivities,
  onToggleRequest,
  onToggleFinance,
}) {
  return (
    <nav className="fixed bottom-0 left-0 right-0 md:left-1/2 md:-translate-x-1/2 mb-2 md:mb-4 px-4 z-50">
      <div className="bg-white rounded-full px-4 md:px-12 py-3 md:py-4 shadow-lg flex items-center justify-between md:gap-16 max-w-screen-sm mx-auto">
        {/* Search Icon */}
        <button
          onClick={() => onToggleSearch()}
          className="text-gray-500 hover:text-gray-700 transition-colors p-2"
        >
          <Search size={20} className="md:w-6 md:h-6 w-5 h-5" />
        </button>

        {/* Notifications Icon */}
        <button
          onClick={() => onToggleActivities()}
          className="text-gray-500 hover:text-gray-700 transition-colors p-2"
        >
          <Bell size={20} className="md:w-6 md:h-6 w-5 h-5" />
        </button>

        {/* Add Button (Centered) with pink glow */}
        <div className="relative -mt-8">
          <div className="absolute inset-0 bg-pink-200 rounded-full blur-xl opacity-70" />
          <button
            onClick={() => onToggleRequest()}
            className="relative bg-orange-500 hover:bg-orange-600 text-white p-3 md:p-4 rounded-full shadow-lg transition-transform hover:scale-110"
          >
            <Plus size={24} className="md:w-6 md:h-6 w-5 h-5" />
          </button>
        </div>

        {/* Dollar Sign Icon */}
        <button
          onClick={() => onToggleFinance()}
          className="text-gray-500 hover:text-gray-700 transition-colors p-2"
        >
          <DollarSign size={20} className="md:w-6 md:h-6 w-5 h-5" />
        </button>

        {/* Profile Icon */}
        <button
          onClick={() => onToggleAccount()}
          className="text-gray-500 hover:text-gray-700 transition-colors p-2"
        >
          <User size={20} className="md:w-6 md:h-6 w-5 h-5" />
        </button>
      </div>
    </nav>
  );
}

export default BottomNavbar;
