import { useState } from "react";
// import { useState } from "react";
import "../App.css";
import Sidebar from "../Components/Sidebar";
import Header from "./Header";
import MarketingBoard from "./Marketing Leads";
import LeadsDashboard from "./Lead Details/Lead Detail";
import Calendar from "./Calendar/Calendar";

function MarketingPortal({ onLogOut }) {
  //   const [eventDetail, setEventDetail] = useState();
  const [activeTab, setActiveTab] = useState("overview");
  if (activeTab === "overview") {
    return (
      <Sidebar
        activeTab={activeTab}
        setActiveTab={setActiveTab}
        onLogOut={onLogOut}
      >
        <Header title="Marketing" showWelcome={true}></Header>

        <MarketingBoard></MarketingBoard>
      </Sidebar>
    );
  }
  if (activeTab === "leads") {
    return (
      <Sidebar
        activeTab={activeTab}
        setActiveTab={setActiveTab}
        onLogOut={onLogOut}
      >
        <Header title="Leads & Tours" showWelcome={false}></Header>

        <LeadsDashboard></LeadsDashboard>
      </Sidebar>
    );
  }
  if (activeTab === "calendar") {
    return (
      <Sidebar
        activeTab={activeTab}
        setActiveTab={setActiveTab}
        onLogOut={onLogOut}
      >
        <Header title="Calendar" showWelcome={false}></Header>
        <Calendar></Calendar>
      </Sidebar>
    );
  }
  return (
    <Sidebar
      activeTab={activeTab}
      setActiveTab={setActiveTab}
      onLogOut={onLogOut}
    ></Sidebar>
  );
}

export default MarketingPortal;
